import * as React from 'react';
import styled from 'styled-components';
import { ContactMe } from './ContactMe';

const DETAILS_HIDDEN_MARGIN = -15;
const DETAILS_SHOWN_MARGIN = 5;

interface IHeaderState {
  contactDetailsOpened: boolean;
  contactDetailsMargin: number;
}

export class Header extends React.Component<{}, IHeaderState> {
  public readonly state: IHeaderState = {
    contactDetailsOpened: false,
    contactDetailsMargin: DETAILS_HIDDEN_MARGIN
  };

  public render() {
    return (
      <HeaderWrapper>
        <ContactMe
          isOpened={this.state.contactDetailsOpened}
          rightMargin={this.state.contactDetailsMargin}
          handleShow={this.showContactDetails}
          handleHide={this.hideContatDetails}
        ></ContactMe>
      </HeaderWrapper>
    );
  }

  private showContactDetails = () => {
    if (this.state.contactDetailsOpened) {
      return;
    }

    this.setState({
      contactDetailsOpened: true,
      contactDetailsMargin: DETAILS_SHOWN_MARGIN
    });
  };

  private hideContatDetails = () => {
    if (!this.state.contactDetailsOpened) {
      return;
    }

    this.setState({
      ...this.state,
      contactDetailsMargin: DETAILS_HIDDEN_MARGIN
    });

    setTimeout(
      () =>
        this.setState({
          ...this.state,
          contactDetailsOpened: false
        }),
      800
    );
  };
}

const HeaderWrapper = styled.div`
  position: fixed;
  z-index: 0;
  top: 0;
  left: 30vw;
  width: 60vw;
  height: 10vh;
`;
